import 'bootstrap/dist/css/bootstrap.min.css'
import {useState, useEffect} from 'react'
import Web3 from 'web3'
import abis from './utils/abis'

import PoolInfo from './components/PoolInfo'
import MarketsInfo from './components/MarketsInfo'
import SymbolsInfo from './components/SymbolsInfo'
import UserInfo from './components/UserInfo'
import OtherInfo from './components/OtherInfo'
import Decoder from './components/Decoder'
import {Button, Form} from "react-bootstrap";

const ONE = new Web3.utils.BN('1000000000000000000')
const ZERO_ADDRESS = '0x0000000000000000000000000000000000000000'
const MAX = '0XFFFFFFFFFFFFFFFFFFFFFFFFFFFFFFFFFFFFFFFFFFFFFFFFFFFFFFFFFFFFFFFF'

// const vaultAddress = "0x05111e862280c8b135bcb5ee173c557f3e1bbcd8"
// const usdtAddress = "0xc8728eb28012c9e79158600aad8aadf02ea3b233"

const vaultAddress = "0xa9514eD83CaAB060F1a22fcbfe2Bd9243868A128"
const usdtAddress = "0x5eCC40d6416eD9E687DCbdd958C2f7C74B3a3Ad7"



function nn(value, unit='ether') {
    return value ? Web3.utils.fromWei(value.toString(), unit) : undefined
}

function bb(value, unit='ether') {
    return value ? Web3.utils.toWei(value.toString(), unit) : undefined
}

function App() {

    const web3 = new Web3(window.ethereum)
    console.log('web3',web3)
    const vault = new web3.eth.Contract(abis.Vault, vaultAddress)
    const usdt  = new web3.eth.Contract(abis.TERC20, usdtAddress)


    const [data, setData] = useState({
        account: ZERO_ADDRESS,
        wallet_balance: 0,
        vault_balance: 0
    })

    useEffect(() => {(async () => {
        const account = await getAccount()
        const wallet_balance = await getUsdtBalance(account)
        const vault_balance = await getVaultBalance(account)
        console.log(account, wallet_balance, vault_balance)
        setData({account, wallet_balance, vault_balance})
    })()}, [])

    const update = async () => {
        const account = await getAccount()
        console.log(account)
        const wallet_balance = await getUsdtBalance(account)
        const vault_balance = await getVaultBalance(account)
        setData({account, wallet_balance, vault_balance})
    }
    const getContract = (abiname, address) => {
        return new web3.eth.Contract(abis[abiname], address)
    }

    const getAccount = async () => {
        const account = Web3.utils.toChecksumAddress((await window.ethereum.request({method: 'eth_requestAccounts'}))[0])
        console.log('getAccount', account)
        return account
    }

    const getUsdtBalance = async (account) => {
        const usdt = getContract('TERC20', usdtAddress)
        const balance = nn(await usdt.methods.balanceOf(account).call())
        return balance
    }

    const getVaultBalance = async (account) => {
        const vault = getContract('Vault', vaultAddress)
        const balance = nn(await vault.methods.balances(account, usdtAddress).call())
        return balance
    }


    const onMint = async () => {
        console.log('onMINT')
        console.log(data)
        await usdt.methods.mint(data.account, bb(10000)).send({from: data.account})
        await update()
    }

    const onApprove = async () => {
        await usdt.methods.approve(vaultAddress, MAX).send({from: data.account})
        await update()
    }

    const onDeposit = async (amount) => {
        await vault.methods.deposit(usdtAddress, bb(amount)).send({from: data.account})
        await update()
    }

    const [amount, setAmount] = useState('')

    return (
        <div className="App">
            <h1>Rubydex Testnet</h1>
            <br/>
            <div>
            <Form.Label>Mint Test USDT:</Form.Label>
            <Button onClick={() => onMint()}>Mint</Button>
            </div>
            <br/>
            <div>
            <Form.Label>Deposit Amount:</Form.Label>
            <Form.Control value={amount} onChange={(e) => {setAmount(e.target.value)}} />
            <Button onClick={() => onApprove()}>Approve USDT</Button>
            <Button onClick={() => onDeposit(amount)}>Deposit</Button>
            </div>
        </div>
    )

}

export default App
